<template>
<div @click="$emit('click')"  class="button">
    <div>
      <slot>+</slot>
    </div>
</div>
</template>

<script>
export default {
  name: 'float-button'
}
</script>

<style scoped>
.button {
  width: 55px;
  height: 55px;
  border-radius: 50%;
  position: fixed;
  background: #FF8148;
  bottom: 36px;
  right: 36px;
  cursor: pointer;
  box-shadow: 0px 2px 5px #666;
  font-size: 2rem;
  color: white;
  text-align: middle;
}

.button > div {
  color: white;
  position: absolute;
  top: 0;
  display: block;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  padding: 0;
  margin: 0;
  line-height: 55px;
  font-size: 38px;
  font-family: 'Roboto';
  font-weight: 300;
}

</style>


