<template>
<div>
    <navigation
        :username="username"
        :active-view="activeView"
        @changeView="v => changeView(v)" 
        @signIn="openSignInModal"
        @signOut="sendSignOutRequest" />
    <list>
        <div slot="contents" v-if="topics != null && topics.length == 0" class="placeholder"> Nothing here </div>
        <topic slot="contents" v-else v-for="topic in topics"
            :key="topic.id"
            :id="topic.id"
            :title="topic.title"
            :link="topic.link"
            :author="topic.author"
            :upvote="topic.upvote"
            :downvote="topic.downvote"
            :submissionTime="topic.submissionTime"
            @changeView="v => changeView(v)"
            @vote="dir => sendVoteRequest({id: topic.id, dir})" />
        <more v-if="nextCursor" slot="end" @more="loadMore" />
    </list>
    <float-button @click="openTopicModal">+</float-button>
    <sign-in-modal
        v-if="showSignInModal"
        :errorMessage="modalErrorMessage"
        @cancel="closeSignInModal"
        @login="sendSignInRequest" />
    <topic-modal
        v-if="showTopicModal"
        :errorMessage="modalErrorMessage"
        @cancel="closeTopicModal"
        @submitTopic="sendSubmitRequest" />
</div>
</template>

<script>
import {mapState, mapActions} from 'vuex';

import Navigation from './components/Navigation.vue';
import List from './components/List.vue';
import Topic from './components/Topic.vue';
import More from './components/More.vue';
import FloatButton from './components/FloatButton.vue';

import SignInModal from './SignInModal.vue';
import TopicModal from './TopicModal.vue';

export default {
    name: 'app',
    components: {
        'navigation': Navigation,
        'list': List,
        'topic': Topic,
        'more': More,
        'float-button': FloatButton,
        'sign-in-modal': SignInModal,
        'topic-modal': TopicModal
    },
    computed: mapState(['username', 'activeView', 'topics', 'nextCursor', 'showSignInModal', 'showTopicModal', 'modalErrorMessage']),
    methods: mapActions(
        ['signIn', 'signOut', 
         'changeView', 'loadMore', 'sendVoteRequest', 
         'openSignInModal', 'closeSignInModal', 'sendSignInRequest', 'sendSignOutRequest',
         'openTopicModal', 'closeTopicModal', 'sendSubmitRequest']),
} 
</script>

<style>
</style>


