<template>
<button @click="$emit('click')" class="button">{{label}}</button>
</template>

<script>
export default {
    name: 'secondary-button',
    props: {
        label: {
            type: String,
            default: 'Cancel'
        }
    }
}
</script>

<style scoped>
.button {
    width: 96px;
    height: 48px;
    border-width: 0;
    outline: none;
    border-radius: 2px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, .6);
    background-color: white;
    transition: background-color .3s;
    color: #FF8148;
    cursor: pointer;
    font-size: 1rem;
    margin: 0.25rem;
}

.button:hover {
    color: #DC5315;
}
</style>


