<template>
<div class="modal-mask">
    <div class="modal-wrapper">
        <div class="modal-container">
            <div class="modal-header">
                <slot name="header">
                    <h3>May I know who are you?</h3>
                </slot>
            </div>
            <div class="modal-body">
                <div v-if="errorMessage" class="errMsg">{{ errorMessage }}</div>
                <input v-model="name" @keyup.enter="$emit('login')" class="name" type="text" placeholder="Tell me your name" />
            </div>
            <div class="modal-footer">
                <secondary-button @click="$emit('cancel')">Cancel</secondary-button>
                <primary-button @click="$emit('login', name)">Submit</primary-button>
            </div>
        </div>
    </div>    
</div>
</template>

<script>
import PrimaryButton from './components/PrimaryButton.vue';
import SecondaryButton from './components/SecondaryButton.vue';

export default {
    name: 'sign-in-modal',
    components: {
        'primary-button': PrimaryButton,
        'secondary-button': SecondaryButton
    },
    props: {
        errorMessage: { type: String }
    },
    data: function() {
        return { name: null };
    }
}
</script>

<style scoped>
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 512px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
}

.modal-header h3 {
    margin-top: 0;
    color: #FF8148;
}

.modal-body {
    margin: 0;
}

.modal-body input[type='text'] {
    font-size: 1rem;
    height: 1.5rem;
    width: 505px;
}

.modal-footer {
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.errMsg {
    margin: 0.5em 0;
    color: red;
}
</style>


