<template>
<nav>
    <div class="usr">
        <a v-if="signedIn" @click="changeView('author:' + username)" href="#" class="usr-name">Hi, {{ username }} </a>
        <a v-if="signedIn" @click="signOut" href="#">sign out</a>
        <a v-else @click="signIn" href="#">sign in</a>
    </div>
    <div class="app">
        <div class="app-name">What<span>&#39;</span>s up</div>
        <div class="app-list-wrapper">
            <a :class="hotClass" @click="changeView('hot')" href="#">Hot</a>
            <a :class="topClass" @click="changeView('top')" href="#">Top</a>
            <a :class="newClass" @click="changeView('new')" href="#">New</a>
            <a :class="conClass" @click="changeView('controversial')" href="#">Controversial</a>
        </div>
    </div>
</nav>
</template>

<script>
export default {
    name: 'navigation',
    props: {
        username: {
            type: String
        },
        activeView: {
            type: String,
            default: 'hot'
        }
    },
    computed: {
        signedIn: function () { return this.username; },
        hotClass: function () { return [(this.activeView === 'hot')? 'active' : '', 'app-list']; },
        topClass: function () { return [(this.activeView === 'top')? 'active' : '', 'app-list']; },
        newClass: function () { return [(this.activeView === 'new')? 'active' : '', 'app-list']; },
        conClass: function () { return [(this.activeView === 'controversial')? 'active' : '', 'app-list']; }
    },
    methods: {
        changeView: function(view) {
            console.log('changeView: ', view);
            this.$emit('changeView', view);
        },
        signOut: function() {
            this.$emit('signOut');
        },
        signIn: function() {
            console.log('sign in');
            this.$emit('signIn');
        }
    }
}
</script>

<style scoped>
div {
    margin: 0px;
}

.usr {
    height: 24px;
    padding-right: 12px;
    background-color: #FF8148;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.usr > a {
    color: white;
    margin-left: 0.5rem;
}

.usr > a.usr-name {
    text-decoration: none;
}

.app {
    height: 96px;
    padding: 0 5rem;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.app-list-wrapper {
    margin: 0;
    padding: 0;
}

.app-list {
    margin: 0.5rem;
    font-size: 1.75rem;
    text-decoration: none;
    font-weight: lighter;
}

.app-list:hover {
    color: #FF8148;
}

.active {
    color: #FF8148 !important;
}

.app-name {
    font-size: 2rem;
    color: #4A4A4A;    
}

.app-name > span {
    color: #FF8148;
}

.app-list {
    color: #9B9B9B;
}
</style>


