<template>
<button @click="$emit('click')" class="button">
    <slot>Submit</slot>
</button>
</template>

<script>
export default {
    name: 'primary-button'
}
</script>

<style scoped>
.button {
    width: 96px;
    height: 48px;
    border-width: 0;
    outline: none;
    border-radius: 2px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, .6);
    background-color: #FF8148;
    transition: background-color .3s;
    color: white;
    cursor: pointer;
    font-size: 1rem;
    margin: 0.25rem;
}

.button:hover {
    background-color: #DC5315;
}
</style>


