import 'es6-promise/auto'

import Vue from 'vue';
import Vuex from 'vuex';

import axios from 'axios';
import qs from 'qs';

import App from './App.vue';
import TopicModal from './TopicModal.vue';

Vue.use(Vuex);

const baseUrl = (process.env.NODE_ENV === 'production')? '' : 'http://localhost:8000';

let scrollOffset = 0;

const store = new Vuex.Store({
  state: {
    username: null,
    topics: null,
    activeView: 'hot',
    nextCursor: null,
    showSignInModal: false,
    showTopicModal: false,
    modalErrorMessage: null
  },
  mutations: {
    topics: function (state, {view, page}) {
      state.activeView = view;
      state.topics = page.contents;
      state.nextCursor = page.next;
    },
    moreTopics: function (state, page) {
      state.topics.push.apply(state.topics, page.contents);
      state.nextCursor = page.next;
      window.scrollTo(0, scrollOffset);
    },
    signInModal: function (state, visibility) {
      state.showSignInModal = visibility;
      state.modalErrorMessage = null;
    },
    signIn: function (state, name) {
      state.showSignInModal = false;
      state.modalErrorMessage = null;
      state.username = name;
    },
    modalError: function (state, message) {
      state.modalErrorMessage = message;
    },
    signOut: function (state) {
      state.username = null;
    },
    topicModal: function (state, visibility) {
      state.showTopicModal = visibility;
      state.modalErrorMessage = null;
    },
    vote: function (state, {id, dir}) {
      state.topics = state.topics.map(t => {
        if (t.id === id) {
          if (dir === 1) {
            t.upvote++;
          } else if (dir === - 1) {
            t.downvote++;
          }
        }
        return t;
      });
    }
  },
  actions: {
    changeView: function ({ commit, state }, view) {
      if (view.startsWith('author:')) {
        axios.get(baseUrl + '/listing/user/' + view.slice(7))
        .then(response => commit('topics', { view: view, page: response.data }))
        .catch(reason => console.error(reason.response.data));
      } else {
        axios.get(baseUrl + '/listing/' + view)
        .then(response => commit('topics', { view: view, page: response.data }))
        .catch(reason => console.error(reason.response.data));
      }
    },
    loadMore: function ({ commit, state }) {
      scrollOffset = window.pageYOffset;
      if (state.activeView.startsWith('author:')) {
        axios.get(baseUrl + '/listing/user/' + state.activeView.slice(7) + "?after=" + state.nextCursor)
        .then(response => commit("moreTopics", response.data))
        .catch(reason => console.error(reason.response.data));
      } else {
        axios.get(baseUrl + '/listing/' + state.activeView + "?after=" + state.nextCursor)
        .then(response => commit("moreTopics", response.data))
        .catch(reason => console.error(reason.response.data));
      }
    },
    reload: function ({ commit, state }) {
      setTimeout(() => {
      if (state.activeView.startsWith('author:')) {
        axios.get(baseUrl + '/listing/user/' + state.activeView.slice(7))
        .then(response => {
          commit('topics', { view: state.activeView, page: response.data });
        })
        .catch(reason => console.error(reason.response.data));
      } else {
         axios.get(baseUrl + '/listing/' + state.activeView)
        .then(response => {
          commit('topics', { view: state.activeView, page: response.data });
        })
        .catch(reason => console.error(reason.response.data));
      }
      }, 0);
    },
    sendVoteRequest: function ({ commit, state }, {id, dir}) {
       axios.post(baseUrl + '/api/vote', qs.stringify({ id, dir }))
       .catch(reason => console.error(reason.response.data));
       commit('vote', {id, dir});
    },
    openSignInModal: function ({ commit }) {
      commit('signInModal', true);
    },
    closeSignInModal: function ({ commit }) {
      commit('signInModal', false);
    },
    sendSignInRequest: function ({ commit }, name) {
      axios.post(baseUrl + '/auth/login', qs.stringify({ username: name }))
      .then(response => commit('signIn', response.data.name))
      .catch(reason => commit('modalError', reason.response.data.message));
    },
    sendSignOutRequest: function ({ commit }) {
      axios.post(baseUrl + '/auth/logout')
      .then(reponse => commit('signOut'))
      .catch(reason => console.error(reason.response.data));
    },
    openTopicModal: function ({ commit, state }) {
      commit('topicModal', true);
      if (!state.username) {
        commit('signInModal', true);
      }
    },
    closeTopicModal: function ({ commit }) {
      commit('topicModal', false);
    },
    sendSubmitRequest: function ({ commit, dispatch }, submission) {
      axios.post(baseUrl + '/api/submit', qs.stringify(submission))
      .then(() => {commit('topicModal', false); store.dispatch('reload'); })
      .catch(reason => commit('modalError', reason.response.data.message));
    }
  }
});

const whatsUp = new Vue({
  el: '#app',
  render: h => h(App),
  store
});

store.dispatch('changeView', 'hot');



